import React from "react"
import { Redirect } from '@reach/router'

import posed from "react-pose"

import { withPrefix } from "gatsby";

const Transition = posed.div({
  enter: {
    opacity: 1,
    transition: {
      duration: 600,
      ease: 'easeInOut'
      }
  },
    exit: { 
      opacity: 0,
    transition: {
      duration: 600,
      ease: 'easeInOut'
      },
      
  },
})


class IndexPage extends React.Component {
  constructor(props){
    super(props);

  }



  render(){

    return(
  
      <Transition>
      <Redirect noThrow to={withPrefix('/ziegertreport/')}/>
      </Transition>
    )
  }
}

export default IndexPage
